___CSS_LOADER_EXPORT___.push([module.id, "\n#formSigner{\n\twidth: 60%;\n\ttext-align: left;\n\tmargin: 20px;\n}\n.form-group > input[type='text'],\n.form-group > input[type='email'] {\n\twidth: 100%;\n}\n#path {\n\twidth: 80%;\n}\n#pickFromCloud{\n\tdisplay: inline-block;\n\tmargin: 16px;\n\tbackground-position: 16px center;\n\tpadding: 12px;\n\tpadding-left: 44px;\n}\n.modal_content {\n\ttext-align: center;\n\tmargin: 40px;\n}\n", "",{"version":3,"sources":["webpack://src/FormSigner.vue"],"names":[],"mappings":";AAgMA;CACA,UAAA;CACA,gBAAA;CACA,YAAA;AACA;AAEA;;CAEA,WAAA;AACA;AAEA;CACA,UAAA;AACA;AAEA;CACA,qBAAA;CACA,YAAA;CACA,gCAAA;CACA,aAAA;CACA,kBAAA;AACA;AAEA;CACA,kBAAA;CACA,YAAA;AACA","sourcesContent":["<template>\n\t<div id=\"formSigner\" class=\"form-signer\">\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"hosts\">{{ t('signer', 'Email') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"hosts\"\n\t\t\t\tref=\"hosts\"\n\t\t\t\tv-model=\"signature.hosts\"\n\t\t\t\ttype=\"email\"\n\t\t\t\t:disabled=\"updating\">\n\t\t</div>\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"commonName\">{{ t('signer', 'Nome (CN)') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"commonName\"\n\t\t\t\tref=\"commonName\"\n\t\t\t\tv-model=\"signature.commonName\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:disabled=\"updating\">\n\t\t</div>\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"country\">{{ t('signer', 'Pas (C)') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"country\"\n\t\t\t\tref=\"country\"\n\t\t\t\tv-model=\"signature.country\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:disabled=\"updating\">\n\t\t</div>\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"organization\">{{ t('signer', 'Organizao (O)') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"organization\"\n\t\t\t\tref=\"organization\"\n\t\t\t\tv-model=\"signature.organization\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:disabled=\"updating\">\n\t\t</div>\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"organizationUnit\">{{ t('signer', 'Unidade da organizao (OU)') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"organizationUnit\"\n\t\t\t\tref=\"organizationUnit\"\n\t\t\t\tv-model=\"signature.organizationUnit\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:disabled=\"updating\">\n\t\t</div>\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"password\">{{ t('signer', 'Senha da assinatura') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"password\"\n\t\t\t\tv-model=\"signature.password\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:disabled=\"updating\">\n\t\t</div>\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"path\">{{ t('signer', 'Armazenamento da assinatura') }}</label>\n\t\t\t<div>\n\t\t\t\t<input\n\t\t\t\t\tid=\"path\"\n\t\t\t\t\tref=\"path\"\n\t\t\t\t\tv-model=\"signature.path\"\n\t\t\t\t\ttype=\"text\"\n\t\t\t\t\t:disabled=\"1\">\n\t\t\t\t<button\n\t\t\t\t\tid=\"pickFromCloud\"\n\t\t\t\t\t:class=\"'icon-folder'\"\n\t\t\t\t\t:title=\"t('signer', 'Selecionar pasta onde assinatura ser salva')\"\n\t\t\t\t\t:disabled=\"updating\"\n\t\t\t\t\t@click.stop=\"pickFromCloud\">\n\t\t\t\t\t{{ t('signer', 'Selecionar Pasta') }}\n\t\t\t\t</button>\n\t\t\t</div>\n\t\t</div>\n\t\t<input\n\t\t\ttype=\"button\"\n\t\t\tclass=\"primary\"\n\t\t\t:value=\"t('signer', 'Gerar Assinatura')\"\n\t\t\t:disabled=\"updating || !savePossible\"\n\t\t\t@click=\"saveSignature\">\n\t\t<Modal\n\t\t\tv-if=\"modal\"\n\t\t\tdark=\"\"\n\t\t\t@close=\"closeModal\">\n\t\t\t<div class=\"modal_content\">\n\t\t\t\t{{ t('signer','Assinatura gerada e disponivel em ') }} {{ signature.path }} !\n\t\t\t</div>\n\t\t</Modal>\n\t</div>\n</template>\n\n<script>\nimport '@nextcloud/dialogs/styles/toast.scss'\nimport { generateUrl } from '@nextcloud/router'\nimport axios from '@nextcloud/axios'\nimport { showError, getFilePickerBuilder } from '@nextcloud/dialogs'\nimport Modal from '@nextcloud/vue/dist/Components/Modal'\nimport { translate as t } from '@nextcloud/l10n'\nexport default {\n\tname: 'FormSigner',\n\tcomponents: {\n\t\tModal,\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tsignature: {\n\t\t\t\tcommonName: '',\n\t\t\t\thosts: '',\n\t\t\t\tcountry: '',\n\t\t\t\torganization: '',\n\t\t\t\torganizationUnit: '',\n\t\t\t\tpath: '',\n\t\t\t\tpassword: '',\n\t\t\t},\n\t\t\tupdating: false,\n\t\t\tloading: true,\n\t\t\tmodal: false,\n\t\t}\n\t},\n\tcomputed: {\n\t\tsavePossible() {\n\t\t\treturn (\n\t\t\t\tthis.signature\n                && this.signature.commonName !== ''\n                && this.signature.hosts !== ''\n                && this.signature.country !== ''\n                && this.signature.organization !== ''\n                && this.signature.organizationUnit !== ''\n                && this.signature.password !== ''\n                && this.signature.path !== ''\n\t\t\t)\n\t\t},\n\t},\n\tasync mounted() {\n\t\tthis.loading = false\n\t\tthis.$refs.hosts.focus()\n\t},\n\n\tmethods: {\n\t\tasync saveSignature() {\n\t\t\tthis.updating = true\n\t\t\ttry {\n\t\t\t\tconst response = await axios.post(\n\t\t\t\t\tgenerateUrl('/apps/signer/api/0.1/signature/generate'),\n\t\t\t\t\tthis.signature\n\t\t\t\t)\n\t\t\t\tif (!response.data || !response.data.signature) {\n\t\t\t\t\tthrow new Error(response.data)\n\t\t\t\t}\n\t\t\t\tthis.signature.path = response.data.signature\n\t\t\t\tthis.showModal()\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t\tshowError(t('signer', 'No foi possivel criar assinatura'))\n\t\t\t}\n\t\t\tthis.updating = false\n\t\t},\n\t\tshowModal() {\n\t\t\tthis.modal = true\n\t\t},\n\t\tcloseModal() {\n\t\t\tthis.modal = false\n\t\t\tthis.signature = {\n\t\t\t\tcommonName: '',\n\t\t\t\thosts: '',\n\t\t\t\tcountry: '',\n\t\t\t\torganization: '',\n\t\t\t\torganizationUnit: '',\n\t\t\t\tpath: '',\n\t\t\t\tpassword: '',\n\t\t\t}\n\t\t},\n\t\tpickFromCloud() {\n\t\t\tconst picker = getFilePickerBuilder(t('signer', 'Escolha uma pasta para armazenar a assinatura'))\n\t\t\t\t.setMultiSelect(false)\n\t\t\t\t.addMimeTypeFilter('httpd/unix-directory')\n\t\t\t\t.setModal(true)\n\t\t\t\t.setType(1)\n\t\t\t\t.allowDirectories(true)\n\t\t\t\t.build()\n\n\t\t\tpicker.pick().then((path) => {\n\t\t\t\tif (!path) {\n\t\t\t\t\tpath = '/'\n\t\t\t\t}\n\t\t\t\tthis.signature.path = path\n\t\t\t})\n\t\t},\n\t},\n}\n</script>\n<style>\n#formSigner{\n\twidth: 60%;\n\ttext-align: left;\n\tmargin: 20px;\n}\n\n.form-group > input[type='text'],\n.form-group > input[type='email'] {\n\twidth: 100%;\n}\n\n#path {\n\twidth: 80%;\n}\n\n#pickFromCloud{\n\tdisplay: inline-block;\n\tmargin: 16px;\n\tbackground-position: 16px center;\n\tpadding: 12px;\n\tpadding-left: 44px;\n}\n\n.modal_content {\n\ttext-align: center;\n\tmargin: 40px;\n}\n</style>\n"],"sourceRoot":""}]);
